@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;800&display=swap);
/* 
Author: Susie Stanley
Purpose: Sidebar/AppBar styling

Site colors used:
Light Blue: #c8e6ea
Light Blue: #c8e6ea68 (faded)
Dark Blue: rgb(82, 207, 223) Details button border
Red: #D61212
Light Gray: rgba(211, 211, 211, 0.468) Dashboard Connections header
Dark Gray: #2b2b2b font color throughout
Shadow: box-shadow: 5px 5px 5px lightgray;
Bright Blue: #c4f7fe highlights, hover on links, and buttons
*/

.appbar-flex {
  background-color: white;
  padding: 20px 5px;
  margin: 0px;
  border-radius: 20px 20px 0px 0px;
  box-shadow: 5px 5px 5px lightgray;
}

.toolbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px;
  margin: 0px 0px;
}

.toolbar-left {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 0px 30px 0px 20px;
}

.logo-wrapper {
  display: flex;
  flex-direction: row;
  align-content: center;
  padding-left: 40px;
}

.logo {
  width: 500px;
  margin: auto 0px;
}

.toolbar-right {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  /* border: 1px solid black; */
}

.user-avatar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  /* border: 1px solid red; */
}

.user-avatar a {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-self: center;
  margin: auto 0px;
}

.avatar {
  margin-right: 20px;
}

.avatar-photo {
  align-self: center;
  margin: 0px 10px;
  border: 3px solid lightgray;
  border-radius: 35px;
  width: 70px;
  height: 70px;
}

.logout {
  margin: auto 20px;
  font-size: 22px;
  font-weight: 500;
  padding: 5px 15px;
  background-color: #c8e6ea;
  border-radius: 5px;
  border: 2px solid lightgray;
  cursor: pointer;
}

.logout:hover {
  border: 2px solid darkgray;
  background-color: #c4f7fe;
}

.menu-bars {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px;
  font-size: 32px;
  color: #000000;
}

.menu-icon {
  align-self: center;
}

.menu-close {
  margin-left: 20px;
  font-size: 32px;
  /* color: #fff; */
}

/* start of side menu styling */
.side-menu {
  /* background-color: white; */
  /* height: 90vh; */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 150px;
  position: absolute;
  /* top: 100; */
  left: -100%;
  transition: 850ms;
  height: 100%;
  box-shadow: 3px 0px 5px rgba(0, 0, 0, 0.2);
  /* border: 1px solid red; */
}

.side-menu.active {
  left: 0;
  transition: 350ms;
  z-index: 5; /* z-index makes side-menu cover everything behind it (heart checkbox) */
}

.sidebar-white {
  background-color: white;
  /* padding-bottom: 30px; */
  /* height: 1045px; */
  height: 100%;
  /* border: 1px solid aqua; */
}

.side-text {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 0px;
  margin: 0px 5px;
  list-style: none;
  height: 70px;
  width: 280px;
}

.side-text a {
  text-decoration: none;
  color: black;
  font-size: 24px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0px 0px 0px 20px;
  border-radius: 4px;
  border: 2px solid lightgray;
}

.side-text a:hover {
  background-color: #c4f7fe;
  border: 2px solid darkgray;
}

.side-menu-items {
  padding: 0px;
  margin: 0px;
}

.sidebar-toggle {
  background-color: white;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin: 0px 20px;
}

.close {
  color: white;
  width: 30px;
}

/* 
Author: Susie Stanley
Purpose: Global styling

Site colors used:
Light Blue: #c8e6ea
Light Blue: #c8e6ea68 (faded)
Dark Blue: rgb(82, 207, 223) Details button border
Red: #D61212
Light Gray: rgba(211, 211, 211, 0.468) Dashboard Connections header
Dark Gray: #2b2b2b font color throughout
Shadow: box-shadow: 5px 5px 5px lightgray;
Bright Blue: #c4f7fe highlights, hover on links, and buttons
*/

* {
  box-sizing: border-box;
  /* margin: 5px; */
  padding: 0px;
  font-family: "Poppins", sans-serif;
}

html {
  /* width: 1880px; */
  margin: 0px;
  padding: 0px;
  margin: 0px;
  font-family: "Poppins", sans-serif;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* border: 2px solid blue; */
}

body {
  margin: 0px;
  padding: 0px;
  font-family: "Poppins", sans-serif;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* border: 1px solid red; */
}

/* Hide scrollbar for Chrome, Safari and Opera but still able to scroll */
body::-webkit-scrollbar {
  display: none;
}

#root {
  flex: 1 1;
  align-self: center;
  background-color: #eeeeee;
  padding: 10px 20px;
  margin: 0px;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: flex-start;
}

h2 {
  margin: 5px 0px 10px;
}

/* WelcomeBar styling */
.page {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: rgb(228, 228, 228);
  border-radius: 0px 0px 20px 20px;
  padding: 0px 20px;
  padding-bottom: 30px;
  margin: 10px 0px;
  width: 1800px;
  min-height: 1000px;
  color: #2b2b2b;
  box-shadow: 5px 5px 5px lightgray;
}

.page-title__flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 30px 40px 0px;
  margin: 0px;
}

.page-title__left {
  flex: 1 1;
  font-size: 20px;
  font-weight: 500;
  text-align: left;
}

.welcome-name {
  color: #41c5d8;
  font-size: 22px;
  font-weight: 600;
  padding: 0px;
  margin: 0px;
}

.welcome-name a {
  text-decoration: none;
  color: #41c5d8;
  cursor: pointer;
}

.welcome-back {
  color: #41c5d8;
  font-size: 22px;
  font-weight: 600;
  padding: 0px;
  margin: 0px;
  cursor: pointer;
}

.welcome-back a {
  text-decoration: none;
  color: #41c5d8;
  cursor: pointer;
}

.page-title__headline {
  flex: 1 1;
  font-size: 38px;
  font-weight: 600;
  text-align: center;
}

.page-title__right {
  flex: 1 1;
  font-size: 20px;
  font-weight: 500;
  text-align: right;
}

.todays-date {
  text-align: right;
  margin: 0px;
  font-weight: 500;
}

/* all boards use this styling */
.section-flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  padding: 0px;
  margin: 0px;
  /* border: 1px solid black; */
}

.section-flex__content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0px;
  margin: 0px;
  /* border: 1px solid red; */
}

.add-connection,
.add-activity,
.add-note,
.add-grocery,
.add-journal,
.add-todo,
.add-provider,
.add-recommendation,
.add-wishlist {
  font-size: 18px;
  font-weight: 600;
  padding: 5px 20px;
  margin: 15px 5px 20px;
  /* border: 2px solid lightgray; */
  border: 2px solid lightgray;
  border-radius: 5px;
  background-color: #c8e6ea;
}

.add-connection:hover,
.add-activity:hover,
.add-note:hover,
.add-grocery:hover,
.add-journal:hover,
.add-todo:hover {
  border: 2px solid darkgray;
  background-color: #c4f7fe;
  cursor: pointer;
}

/* all detail pages use this button styling */
.btn-flex {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.details-btn {
  font-size: 18px;
  font-weight: 500;
  padding: 5px 20px;
  border: 2px solid lightgray;
  border-radius: 5px;
  cursor: pointer;
  background-color: #c8e6ea;
  /* background-color: #c4f7fe; */
  margin: 30px 5px 0px;
}

.details-btn:hover {
  border: 2px solid darkgray;
  background-color: #c4f7fe;
  cursor: pointer;
}

/* all form buttons use this styling */
.form-btns {
  padding: 0px;
  margin: 0px;
}

.form-btn {
  font-size: 20px;
  font-weight: 600;
  background-color: #c8e6ea;
  border: 2px solid lightgray;
  border-radius: 5px;
  padding: 7px 15px;
  margin: 10px;
  cursor: pointer;
}

.form-btn:hover {
  border: 2px solid darkgray;
  background-color: #c4f7fe;
  cursor: pointer;
}

/* Styling for all Create and Edit forms */
.form-flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px;
  margin: 0px;
  /* border: 1px solid red; */
}

.form {
  font-size: 22px;
  padding: 30px 50px;
  margin: 10px;
  background-color: white;
  box-shadow: 2px 3px 7px gray;
  border-radius: 5px;
  border: none;
  /* border: 1px solid black; */
}

input {
  font-size: 20px;
  color: gray;
  padding: 5px;
  margin: 5px;
  border: 1px solid darkgray;
  border-radius: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

input::-webkit-input-placeholder {
  color: rgb(198, 198, 198);
  opacity: 1;
}

input:-ms-input-placeholder {
  color: rgb(198, 198, 198);
  opacity: 1;
}

input::placeholder {
  color: rgb(198, 198, 198);
  opacity: 1;
}

/* .form__group--edit {
  width: 400px;
} */

.form__group--edit:focus {
  outline-color: #c4f7fe;
  /* border: 0; */
}

.asterisk-info {
  font-size: 16px;
  margin: 15px 10px;
}

/* targets date picker font color */
.light-font input {
  color: rgb(171, 171, 171);
}

/* dialog box settings for all except connections */
.dialog {
  width: 600px;
  font-size: 20px;
  font-weight: 500;
  border-radius: 5px;
  border: 4px solid rgb(82, 207, 223);
  background-color: rgb(234, 246, 247);
  padding: 20px;
  margin: 0px auto;
  margin-top: 100px;
  text-align: center;
}

.dialog-forms {
  margin: 0px 0px 10px;
  padding: 0px 0px 20px;
}

/* dialog box settings for connections */
.dialog-c {
  width: 600px;
  font-size: 20px;
  font-weight: 500;
  border-radius: 5px;
  border: 4px solid rgb(82, 207, 223);
  background-color: rgb(234, 246, 247);
  padding: 20px;
  margin: 0px auto;
  margin-top: 600px;
  text-align: center;
}

.dialog-forms__connections {
  margin: 0px 0px 10px;
  padding: 0px 0px 20px;
}

.button-close {
  font-size: 14px;
  font-weight: 600;
  background-color: rgba(211, 211, 211, 0.708);
  border: 2px solid rgb(82, 207, 223);
  border-radius: 5px;
  padding: 5px 10px;
  margin: 0px 5px 0px;
  cursor: pointer;
}

/* 
Author: Susie Stanley
Purpose: Dashboard styling

Site colors used:
Light Blue: #c8e6ea
Light Blue: #c8e6ea68 (faded)
Dark Blue: rgb(82, 207, 223) Details button border
Red: #D61212
Light Gray: rgba(211, 211, 211, 0.468) Dashboard Connections header
Dark Gray: #2b2b2b font color throughout
Shadow: box-shadow: 5px 5px 5px lightgray;
Bright Blue: #c4f7fe highlights, hover on links, and buttons
*/

h2 {
  font-weight: 600;
}

.bold {
  font-size: 20px;
  font-weight: 600;
}

.med-bold {
  font-weight: 500;
  margin: 5px;
}

.dashboard-grid {
  margin: 15px 5px 5px;
}

/* this holds activities, groceries, notes, journal, & todos */
.dashboard-grid__row1 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0px;
  margin: 0px;
  min-width: 1350px;
}

/* this holds activities, groceries, notes, & journal */
.dashboard-grid__row1__col1 {
  flex: 2 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px;
  margin: 0px;
  /* border: 1px solid purple; */
}

/* this holds activities & groceries */
.dashboard-grid__row1__col1__row1 {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 0px;
  margin: 0px;
  /* border: 1px solid blue; */
}

/* this targets the white squares in the grid */
.activity-container,
.grocery-container,
.journal-container,
.note-container {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding-top: 10px;
  margin: 10px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 2px 3px 7px gray;
  min-height: 270px;
}

/* this targets the section headline as link */
.activity-container a,
.grocery-container a,
.journal-container a,
.note-container a {
  text-decoration: none;
  color: #2b2b2b;
  margin: 0px;
}

/* this holds notes & journal */
.dashboard-grid__row1__col1__row2 {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 0px;
  margin: 0px;
  /* border: 1px solid red; */
}

/* this holds todos */
.dashboard-grid__row1__col2 {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 10px 0px;
  margin: 10px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 2px 3px 7px gray;
  /* border: 1px solid black; */
}

.dashboard-grid__row1__col2 a {
  text-decoration: none;
  color: #2b2b2b;
  margin: 0px;
}

/* To-Do dash title */
.dashboard-grid__row1__col2 h2 {
  text-align: center;
  margin-bottom: 8px;
}

/* this is outer div of connections */
.dashboard-grid__row2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px;
  margin: 0px;
  min-width: 1350px;
  /* border: 3px solid pink; */
}

/* this is inner div of connections */
.dashboard-grid__row2__row1 {
  display: flex;
  flex-direction: column;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  background-color: white;
  min-height: 250px;
  box-shadow: 2px 3px 7px gray;
}

.dashboard-grid__row2__row1 a {
  text-decoration: none;
  color: #2b2b2b;
  margin: 5px auto 0px;
}

.dashboard-grid__row1__col2 h2 {
  text-align: center;
  margin-bottom: 8px;
}

.dashboard-grid__row2__row1 h2 {
  text-align: center;
  margin-bottom: 20px;
}

/* To-Do Dash styling */
.dash-todo__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 30px;
  /* border: 1px solid red; */
}

.dash-todo {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-self: stretch;
  padding: 10px 20px;
  margin: 9px 0px;
  background-color: #c8e6ea68;
  border-radius: 5px;
  /* border: 1px solid black; */
}

.dash-todo__col1 {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* border: 1px solid red; */
}

.dash-todo__col2 {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  white-space: nowrap;
  align-self: center;
  margin: 0px 5px 0px 0px;
  /* border: 1px solid blue; */
}

.dash-todo__title {
  font-size: 20px;
  font-weight: 500;
  padding-bottom: 3px;
  margin: 5px 5px 0px;
  white-space: nowrap;
}

.dash-todo__bywhen {
  font-size: 18px;
  font-weight: 400;
  padding: 0px;
  margin: 0px 5px 5px;
  white-space: nowrap;
}

.dash-todo__bywhen__red {
  font-size: 18px;
  font-weight: 400;
  color: #d61212;
  padding: 0px;
  margin: 0px 5px 5px;
  white-space: nowrap;
}

.dash-todo-complete {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0px 30px 0px 10px;
  /* border: 1px solid chartreuse; */
}

.dash-todo-complete label {
  padding: 0px 5px;
}

.td-delete-icon {
  color: #2b2b2b;
  margin: auto auto;
  width: 25px;
  height: 25px;
  transition: all 0.2s ease-in-out;
  /* border: 1px solid black; */
}

.td-delete-icon:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  color: #d61212;
  cursor: pointer;
}

input.checkbox-todo {
  width: 20px;
  height: 20px;
  transition: all 0.2s ease-in-out;
}

input.checkbox-todo:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  color: #27c2d7;
  cursor: pointer;
}

/* Activity Dash styling */
.dash-activity__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 10px;
}

.dash-activity__list a {
  text-decoration: none;
  color: #2b2b2b;
}

.dash-activity {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 30px;
  margin: 5px 30px 8px;
  background-color: #c8e6ea68;
  border-radius: 5px;
}

.dash-activity__col1 {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  padding: 0px;
  margin: 0px;
  /* border: 1px solid red; */
}

.dash-activity__wrapper {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px;
  margin: 0px;
  /* border: 1px solid blue; */
}

.dash-activity__name {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-weight: 500;
  padding: 0px;
  margin: 0px;
  text-align: left;
  font-size: 22px;
  /* border: 1px solid black; */
}

.da-tag-image {
  width: 40px;
  height: 40px;
}

.da-tag-icon {
  width: 100%;
}

/* .da-tag-icon:hover {
  cursor: pointer;
} */

.da-inner__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 0px;
  margin: 0px;
  /* border: 1px solid black; */
}

.da-inner__left {
  text-align: left;
  font-size: 18px;
  line-height: 1.3em;
  white-space: nowrap;
  margin: 0px;
  padding: 0px;
}

.da-left-spacer {
  margin-top: 13px;
}

.card-spacer {
  padding: 4px 0px 7px;
}

.transparent {
  color: transparent;
}

.da-inner__right {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  font-size: 18px;
  font-weight: 300;
  text-align: left;
  line-height: 1em;
  margin: 0px 5px;
  max-width: 400px;
  padding: 4px 0px 0px 10px;
  /* border: 1px solid pink; */
}

.da-address__highlight {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  overflow-wrap: break-word;
  padding: 3px 10px;
  margin: 0px 5px;
  /* border: 1px solid green; */
}

.dash-activity__col2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  padding: 7px 5px 0px 0px;
  /* border: 1px solid black; */
}

button.activity-delete {
  flex: 1 1;
  align-self: center;
  border: none;
  background-color: transparent;
  margin: auto auto;
}

.a-delete-icon {
  width: 25px;
  height: 25px;
  transition: all 0.2s ease-in-out;
}

.a-delete-icon:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  color: #d61212;
  cursor: pointer;
}

/* Note Dash styling */
.note-container {
  background-image: url(/lifehacker/static/media/notecard.3eca66e3.svg);
  background-repeat: no-repeat;
  background-position: 40px 25px;
  background-size: 80%;
  min-width: 545px;
  height: 335px;
  /* border: 1px solid black; */
}

.note-spacing {
  margin-top: 10px;
}

.dash-note__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-right: 30px;
  /* border: 1px solid red; */
}

.dash-note__wrapper a {
  padding-right: 50px;
  /* border: 2px solid pink; */
  width: 290px;
}

.dash-note {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: center;
  padding: 0px;
  min-height: 190px;
  /* border: 2px solid blue; */
}

.dash-note__text {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
  margin: 0px;
  width: 200px;
  /* border: 2px solid chartreuse; */
}

.dn-text-wrapper {
  display: flex;
